<template>
	<header class="header">
		<div class="topbar">
			<div class="container">
				<h3>悦视觉-全球婚礼·摄影·旅游·服务平台</h3>
			</div>
		</div>
		<div class="headerbar">
			<div class="container">
				<div class="header-logo">
					<router-link to="/" class="logo">logo</router-link>
				</div>
				<div class="header-link">
					<div class="login">
						<router-link to="/" class="btn-signup">免费注册</router-link>
						<router-link to="/" class="btn-signin">登录</router-link>
					</div>
				</div>
				<div class="header-search">
					<form action="" class="search-form">
						<input type="text" v-model.trim="search" class="search-input" :placeholder="placeholder" autofocus="autofocus"></input>
						<i class="el-icon-search" @click="handleSearch"></i>
					</form>
				</div>
			</div>
		</div>
		<div class="navbar">
			<div class="container">
				<ul class="header-nav">
					<li><router-link to="/">首页</router-link></li>
					<li>
						<router-link to="/" class="relative">
							<!-- <img src="../assets/img/tehui.png" class="tag"> -->
							一价全包
						</router-link>
					</li>
					<li><router-link to="/">国内摄影</router-link></li>
					<li><router-link to="/">国外摄影</router-link></li>
					<li><router-link to="/">旅游线路</router-link></li>
					<li><router-link to="/">服务支持</router-link></li>
					<li><router-link to="/show" class="active">客片展示</router-link></li>
					<li><router-link to="/">关于我们</router-link></li>
				</ul>
				<div class="header-order">
					<router-link to="/" class="my-order">我的订单</router-link>	
				</div>
			</div>
		</div>
	</header>
</template>
<script>
	import { requestExit } from '@/api'
	export default {
		data() {
			return {
				placeholder: '请输入目的地或摄影关键词',
				search: '',
				navBarTop: 0,
			}
		},
		props: ['userInfo'],
		methods: {
			handleSearch() {
				if(!this.search) return;
				this.$notify({
					type: 'warning',
					title: '提示',
					message: `未查询到“${this.search}”的相关内容`
				})
			},
			handleExit() {
				this.$confirm('确定退出本次登录', '提示', {type: 'warning'}).then(() => {
						requestExit().then(res => {
							if(res.data.code === '0001') {
								this.$router.replace( '/login')
							} else {
								this.$message.error(res.data.message)
							}
						})
				}).catch(err => {
					console.log(err)
				})
			},
			enterSearch(e) {
				e.keyCode === 13 && this.handleSearch()
			},
			scroll() {
				// console.log(document.querySelector('.navbar').offsetTop)
				// console.log(document.body.scrollTop)
				let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
				if(scrollTop >= this.navBarTop) {
					document.querySelector('.navbar').className = 'navbar fixed';
				} else {
					document.querySelector('.navbar').className = 'navbar';
				}
			},
		},
		mounted() {
			this.navBarTop = document.querySelector('.navbar').offsetTop || 0;
			document.querySelector('.search-input').focus(); 
			document.querySelector('.search-input').addEventListener('keydown', this.enterSearch)
			document.addEventListener('scroll', this.scroll)
		},
	  beforeDestroy() {
	  	document.removeEventListener('scroll', this.scroll)
	  },
	}
</script>
<style scoped lang="scss">
	.header {
		.topbar {
			background: #f0f0f0;
			h3 {
				padding: 8px 0;
				color: #999;
				font-size: 14px;
				line-height: 1;
				text-align: left;
				letter-spacing: 1px;
			}
		}
		.headerbar {
			line-height: 70px;
			background: #fff;
			.header-logo {
				float: left;
				height: 40px;
				margin-top: 13px;
				.logo {
					display: inline-block;
					width: 120px;
					height: 43px;
					text-indent: -2000em;
					background: url(../assets/img/logo_blue.png) no-repeat center;
					background-size: cover;
				}
			}
			.header-search {
				float: right;
				margin-right: 50px;
				width: 300px;
				height: 36px;
				margin-top: 17px;
				line-height: 36px;
				padding-left: 12px;
				border: 1px solid #00AAEF;
				.search-input {
					width: 255px;
					height: 32px;
					font-size: 16px;
					border: none;
					background: transparent;
				}
				.el-icon-search {
					position: relative;
					right: -5px;
					cursor: pointer;
					color: #999;
				}
			}
			.header-link {
				float: right;
				line-height: 70px;
				color: #fff;
				font-size: 16px;
				a[class^="btn-"] {
					display: inline-block;
					width: 100px;
					height: 30px;
					line-height: 30px;
					text-align: center;
					font-size: 14px;
					vertical-align: middle;
				}
				.btn-signin {
					color: #fff;
					background: #00AAEF;
				}
				.btn-signup {
					color: #00AAEF;
					border: 1px solid #00AAEF;
					&:hover {
						color: #fff;
						background: #00AAEF;
					}
				}
				.user {
			    width: 21px;
			    height: 20px;
			    vertical-align: bottom;
			    margin-right: 5px;
					background-position: -215px 0;
				}
			}
		}
		.navbar {
			background: #00AAEF;
			&.fixed {
				position: fixed;
				top: 0;
				z-index: 9999;
				width: 100%;
				background: rgba(0,170,239,.8);
				box-shadow: 1px 1px 3px 3px rgba(0,0,0,.2);
			}
			.header-nav {
				float: left;
				line-height: 50px;
				li {
					float: left;
					margin-left: 30px;
					font-size: 18px;
					&:first-child {
						margin-left: 0;
					}
					a {
						color: #fff;
						transition: all .3s;
						&:hover, &.active {
	  					padding-bottom: 6px;
							border-bottom: 2px #EFE701 solid;
						}
						.tag {
							position: absolute;
							top: -25px;
							right: -10px;
						}
					}
				}
			}
			.header-order {
				float: right;
				line-height: 50px;
				.my-order {
					display: block;
					padding: 0 70px;
					color: #fff;
					font-size: 16px;
					background: #56CEFF;
				}
			}
		}
		.header-left {
			float: left;
		}
		.header-right {
			float: right;
			height: 47px;
			padding-top: 8px;
		}
		.shopping-cart {
			float: left;
			margin: 0 40px;
			font-size: 24px;
			color: #fff;
			.cart {
			  width: 29px;
			  height: 20px;
			  cursor: pointer;
				background-position: -151px 0;
				&:hover {
					background-position: -121px 0;
				}
			}
		}
	}
</style>


